import { Input, Layout, useTheme } from '@ui-kitten/components';
import { LinearGradient } from 'expo-linear-gradient';
import { useEffect, useRef, useState } from 'react';

import PostsList from './PostsList';
import {
  selectSearchTerm,
  selectSubmittedSearchTerm,
  searchTermChanged,
  searchTermSubmitted,
  selectTag,
  selectRecentSearches,
  recentSearchTermRemoved,
  recentSearchTermSelected
} from './discoverSlice';
import { selectDarkMode } from '../../app/appDataSlice';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import Header from '../../components/Header';
import SearchBar from '../../components/SearchBar';
import { CurrentTermLabel } from '../classPages/ClassesList';

export default function SearchPage({ navigation }) {
  const dispatch = useAppDispatch();

  const searchTerm = useAppSelector(selectSearchTerm);
  const tag = useAppSelector(selectTag);
  const submittedSearchTerm = useAppSelector(selectSubmittedSearchTerm);
  const recentSearches = useAppSelector(selectRecentSearches);

  const inputRef = useRef<Input>(null);

  const darkMode = useAppSelector(selectDarkMode);
  const theme = useTheme();

  useEffect(() => {
    inputRef.current?.focus();
  }, []);

  const onSubmitEditing = async () => {
    dispatch(searchTermSubmitted(searchTerm));
  };

  const onChangeText = (text: string) => {
    dispatch(searchTermChanged(text));
  };

  const handleRemoveRecentSearch = (searchTerm: string) => {
    dispatch(recentSearchTermRemoved(searchTerm));
  };

  const handleClickRecentSearch = (searchTerm: string) => {
    dispatch(recentSearchTermSelected(searchTerm));
  };

  let searchBarBackgroundColor = '';
  if (darkMode) {
    searchBarBackgroundColor = theme['color-basic-800'];
  } else {
    searchBarBackgroundColor = theme['color-basic-100'];
  }

  return (
    <Layout style={{ flex: 1 }}>
      <Layout level="2" style={{ paddingBottom: 15 }}>
        <Header
          level="2"
          center={
            <Layout
              level="2"
              style={{ flex: 1, backgroundColor: 'transparent' }}
            >
              <SearchBar
                inputRef={inputRef}
                inputStyle={{
                  opacity: 1,
                  backgroundColor: searchBarBackgroundColor
                }}
                searchTerm={searchTerm}
                onChangeText={onChangeText}
                onSubmitEditing={onSubmitEditing}
                placeholder="Search Posts..."
                cancelButtonAction={() => navigation.goBack()}
                cancelButtonStatus="basic"
                alwaysShowCancelButton
              />
            </Layout>
          }
          title=""
        />
      </Layout>
      <PostsList
        navigation={navigation}
        tag={tag}
        searchTerm={submittedSearchTerm}
        recentSearches={recentSearches}
        handleRemoveRecentSearch={handleRemoveRecentSearch}
        handleClickRecentSearch={handleClickRecentSearch}
        sponsor
      />
    </Layout>
  );
}
