import React from 'react';
import { LogBox } from 'react-native';

import CalendarRender from './CalendarRender';

LogBox.ignoreAllLogs();

export default function CalendarPage({ navigation }) {
  return (
    <CalendarRender
      route={{
        params: {
          overrideData: []
        }
      }}
    />
  );
}

