import { FontAwesome5, MaterialIcons } from '@expo/vector-icons';
import {
  Button,
  Divider,
  Icon,
  Layout,
  List,
  ListItem,
  Spinner,
  Text,
  useTheme
} from '@ui-kitten/components';
import { useEffect, useRef } from 'react';
import { Keyboard, Platform } from 'react-native';

import { selectDarkMode, selectUserKey } from '../../app/appDataSlice';
import { useAppSelector } from '../../app/hooks';
import { DiscoverPost } from '../../app/types';
import {
  discoverApiSlice,
  postsAdapter,
  postsSelectors,
  useLazyGetDiscoverPostsQuery
} from '../api/discoverApi';
import { usersApiSlice } from '../api/usersApi';
import EventCard from './Cards/EventCard';
import ImageCard from './Cards/ImageCard';
import SponsorCard from './Cards/SponsorCard';
import TextCard from './Cards/TextCard';

export default function PostsList({
  navigation,
  tag,
  searchTerm,
  recentSearches,
  handleRemoveRecentSearch,
  handleClickRecentSearch
}: {
  navigation: any;
  tag?: string;
  searchTerm?: string;
  recentSearches?: string[];
  sponsor?: boolean;
  handleRemoveRecentSearch?: (searchTerm: string) => void;
  handleClickRecentSearch?: (searchTerm: string) => void;
  route?: { params: { sponsor: boolean } };
}) {
  const theme = useTheme();
  const userKey = useAppSelector(selectUserKey);

  const {
    data: { userName }
  } = usersApiSlice.endpoints.getUserInfo.useQueryState(userKey);

  const pageNumber = useRef(0);
  const [fetchPosts, { isFetching, isLoading, isError }] =
    useLazyGetDiscoverPostsQuery();

  const { posts, hasMorePages } =
    discoverApiSlice.endpoints.getDiscoverPosts.useQueryState(
      {
        pageNumber: 1,
        userKey,
        userName,
        tag,
        searchTerm
      },
      {
        selectFromResult: (result) => {
          return {
            hasMorePages: result.data?.hasMorePages,
            posts: postsSelectors.selectAll(
              result.data ?? postsAdapter.getInitialState()
            )
          };
        }
      }
    );

  const fetchFirstPage = async () => {
    pageNumber.current = 1;
    await fetchPosts({
      userKey,
      pageNumber: pageNumber.current,
      userName,
      tag,
      searchTerm
      // sponsor,
    });
  };

  const fetchMorePosts = async () => {
    if (!hasMorePages || isFetching) return;
    pageNumber.current++;
    await fetchPosts({
      userKey,
      pageNumber: pageNumber.current,
      userName,
      tag,
      searchTerm
      // sponsor,
    });
  };

  useEffect(() => {
    fetchFirstPage();
  }, [searchTerm, tag]);

  const PinIcon = (
    <Layout
      style={{
        flexDirection: 'row',
        marginBottom: 0,
        marginTop: 10,
        marginLeft: 10
      }}
    >
      <MaterialIcons
        name="push-pin"
        size={20}
        color={theme['color-primary-500']}
      />
      <Text style={{ color: theme['color-primary-500'] }}>pinned</Text>
    </Layout>
  );

  // Render Content

  const renderItem = ({ item }: { item: DiscoverPost }) => {
    const commonProps = {
      title: item.title,
      tags: item.tags,
      _id: item._id,
      postAuthor: item.userName,
      navigation,
      key: item._id,
      searchTerm,
      searchedTag: tag,
      pinnedIcon: item.pinned ? PinIcon : null
    };

    if (item.isSponsor) {
      const sponsorProps = JSON.parse(item.contentText);
      return <SponsorCard data={sponsorProps} navigation={navigation} />;
    }

    switch (item.type) {
      case 'contentText':
        return (
          <TextCard
            {...commonProps}
            body={item.contentText}
            emoji={item.emoji}
            isUrl={false}
          />
        );
      case 'contentUrl':
        return (
          <TextCard
            {...commonProps}
            body={item.contentUrl}
            emoji={item.emoji}
            isUrl
          />
        );
      case 'contentImage':
        return (
          <ImageCard
            {...commonProps}
            hasLiked={item.hasLiked}
            imageId={item.contentImage}
            postAuthor={item.userName}
            userName={userName}
          />
        );
      case 'contentEvent':
        return (
          <EventCard
            location={item.location}
            date={item.date}
            startTime={item.startTime}
            postAuthor={item.userName}
            userName={userName}
            contentURL={item.contentUrl}
            contentText={item.contentText}
            contentImage={item.contentImage}
            endTime={item.endTime}
            hasLiked={item.hasLiked}
            host={item.host}
            {...commonProps}
          />
        );

      default:
        console.warn('Tried to load an unknown post type:', item?.type);
        return null;
    }
  };

  const renderRecentSearch = ({ item }: { item: string }) => {
    return (
      <ListItem
        onPress={() => {
          handleClickRecentSearch(item);
          Keyboard.dismiss();
        }}
        style={{
          paddingVertical: 10,
          marginVertical: 0,
          backgroundColor: 'transparent'
        }}
        title={item}
        accessoryLeft={() => (
          <Icon
            name="clock"
            style={{
              height: 20,
              color: theme['color-basic-400'],
              marginHorizontal: 10
            }}
          />
        )}
        accessoryRight={(props) => (
          <Button
            appearance="ghost"
            onPress={() => handleRemoveRecentSearch(item)}
            accessoryLeft={<Icon {...props} name="x" pack="feather" />}
          />
        )}
      />
    );
  };

  function FooterSpinner() {
    if (isFetching) {
      return (
        <Layout style={{ alignItems: 'center', paddingVertical: 10 }}>
          <Spinner />
        </Layout>
      );
    }
  }

  let content = <></>;
  if (isLoading) {
    content = (
      <Layout style={{ marginTop: 20, alignItems: 'center' }}>
        <Spinner />
      </Layout>
    );
  } else if (isError) {
    content = (
      <Layout style={{ alignItems: 'center', marginTop: 20 }}>
        <FontAwesome5
          name="exclamation"
          size={50}
          color={theme['color-danger-500']}
        />
        <Text style={{ textAlign: 'center', fontSize: 20 }}>
          Error loading posts
        </Text>
      </Layout>
    );
  } else if (posts.length === 0) {
    content = (
      <Layout style={{ alignItems: 'center', marginTop: 20 }}>
        <FontAwesome5
          name="sad-tear"
          size={50}
          color={theme['color-danger-500']}
        />
        <Text style={{ textAlign: 'center', fontSize: 20 }}>
          No posts found
        </Text>
      </Layout>
    );
  } else if (searchTerm === '' && tag === '') {
    if (recentSearches?.length > 0) {
      content = (
        <Layout style={{ flex: 1 }}>
          <List
            keyboardShouldPersistTaps="handled"
            ListHeaderComponent={() => (
              <Text
                status="success"
                style={{ fontStyle: 'italic', marginLeft: 20 }}
              >
                Recent Searches
              </Text>
            )}
            data={Array.from(recentSearches)}
            renderItem={renderRecentSearch}
          />
        </Layout>
      );
    } else {
      content = (
        <Layout style={{ flex: 1 }}>
          <Layout style={{ marginTop: 30, justifyContent: 'center' }}>
            <Text style={{ textAlign: 'center', fontSize: 20 }}>
              Search for posts
            </Text>
            <Text
              style={{
                textAlign: 'center',
                fontSize: 16,
                color: theme['color-basic-500']
              }}
            >
              Search by title, tag, or college
            </Text>
          </Layout>
        </Layout>
      );
    }
  } else {
    content =
      Platform.OS === 'web' ? (
        <List
          contentContainerStyle={{
            width: '100%',
            maxWidth: 720,
            alignSelf: 'center',
            paddingHorizontal: 10,
            backgroundColor: 'transparent'
          }}
          onRefresh={fetchFirstPage}
          refreshing={isLoading}
          data={posts}
          keyExtractor={(item) => item._id}
          renderItem={renderItem}
          onEndReachedThreshold={0.5}
          onEndReached={fetchMorePosts}
          ListFooterComponent={FooterSpinner}
          ItemSeparatorComponent={null}
        />
      ) : (
        <Layout style={{ flex: 1 }}>
          <List
            contentContainerStyle={{ width: '100%' }}
            onRefresh={fetchFirstPage}
            refreshing={isLoading}
            data={posts}
            keyExtractor={(item) => item._id}
            renderItem={renderItem}
            onEndReachedThreshold={0.5}
            onEndReached={fetchMorePosts}
            ListFooterComponent={FooterSpinner}
            showsVerticalScrollIndicator={false}
            ItemSeparatorComponent={Divider}
          />
        </Layout>
      );
  }

  return <Layout style={{ flex: 1 }}>{content}</Layout>;
}
