import { AntDesign } from '@expo/vector-icons';
import DateTimePicker from '@react-native-community/datetimepicker';
import {
  Button,
  Datepicker,
  Divider,
  Input,
  Layout,
  NativeDateService,
  Text
} from '@ui-kitten/components';
import { useMemo, useState } from 'react';
import { Platform } from 'react-native';

import WrapItems from '../../../components/WrapItems';
import CreateImage from './CreateImagePost';

export default function CreateEvent({
  discoverPostInputs,
  setDiscoverPostInputs,
  TagInput,
  startTime,
  setStartTime,
  endTime,
  setEndTime,
  date,
  setDate
}) {
  // Needed because Android date picker uses a modal
  const [showTimePickerAndroid, setShowTimePickerAndroid] = useState(false);

  const formatDateService = new NativeDateService('en', {
    format: 'MM/DD/YYYY'
  });
  const timePickerOptions = useMemo(
    () => ({
      'Start Time': { time: startTime, setTime: setStartTime },
      'End Time': { time: endTime, setTime: setEndTime }
    }),
    [startTime, setStartTime, endTime, setEndTime]
  );

  const formatAMPM = (date: Date) => {
    let hours = date.getHours();
    let minutes = date.getMinutes() as any;
    const ampm = hours >= 12 ? 'pm' : 'am';
    hours %= 12;
    hours = hours || 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? `0${minutes}` : minutes;
    const strTime = `${hours}:${minutes} ${ampm}`;
    return strTime;
  };

  const Title = (
    <Layout
      style={{
        flexDirection: 'row',
        justifyContent: 'space-evenly',
        alignItems: 'center',
        marginTop: 15
      }}
    >
      <Input
        label="Title"
        placeholder="Post Title"
        value={discoverPostInputs?.title ? discoverPostInputs.title : ''}
        style={{ flex: 5 }}
        onChangeText={(newText) => {
          setDiscoverPostInputs({
            ...discoverPostInputs,
            title: newText
          });
        }}
      />
    </Layout>
  );

  function TimePicker() {
    if (Platform.OS === 'android') {
      return (
        <Layout
          style={{
            flexDirection: 'row',
            justifyContent: 'space-around'
          }}
        >
          {Object.entries(timePickerOptions).map(
            ([label, { time, setTime }], i) => (
              <Layout key={i} style={{ alignItems: 'center' }}>
                <Text category="label" style={{ marginBottom: 5 }}>
                  {label}
                </Text>
                <Button
                  onPress={() => {
                    setShowTimePickerAndroid(true);
                  }}
                >
                  {formatAMPM(time)}
                </Button>
                {showTimePickerAndroid && (
                  <DateTimePicker
                    mode="time"
                    onChange={(change, date) => {
                      setTime(date);
                      setShowTimePickerAndroid(false);
                    }}
                    value={time}
                  />
                )}
              </Layout>
            )
          )}
        </Layout>
      );
    }
    if (Platform.OS === 'ios') {
      return (
        <Layout
          style={{
            flexDirection: 'row',
            justifyContent: 'space-around'
          }}
        >
          {Object.entries(timePickerOptions).map(
            ([label, { time, setTime }], i) => (
              <Layout key={i} style={{ alignItems: 'center' }}>
                <Text category="label" style={{ marginBottom: 5 }}>
                  {label}
                </Text>

                <DateTimePicker
                  mode="time"
                  onChange={(change, date) => {
                    setTime(date);
                  }}
                  value={time}
                />
              </Layout>
            )
          )}
        </Layout>
      );
    }
  }

  if (Platform.OS === 'web') {
    return (
      <Text>
        Creating Event Posts is not supported on the web. Download the app on
        the App Store or Play Store to make an Event Post.
      </Text>
    );
  }

  return (
    <Layout>
      <WrapItems childStyle={{ marginVertical: 10 }}>
        {Title}
        <Input
          label="Location"
          placeholder="Ex: North Dorm, HMC"
          value={discoverPostInputs?.location}
          onChangeText={(newText) => {
            setDiscoverPostInputs({
              ...discoverPostInputs,
              location: newText
            });
          }}
        />
        <Datepicker
          label="Date"
          dateService={formatDateService}
          onSelect={(nextDate) => setDate(nextDate)}
          date={date}
          placement="top"
        />
        <TimePicker />
        <Layout style={{ padding: 6 }}>
          <Divider />
          <AntDesign name="caretdown" style={{ position: 'absolute' }} />
          <AntDesign
            name="caretdown"
            style={{ position: 'absolute', right: 0 }}
          />
          <Layout
            style={{ position: 'absolute', right: '44%', marginBottom: 6 }}
          >
            <Text appearance="hint">Optional</Text>
          </Layout>
        </Layout>

        <Layout>
          <Input
            label="Link"
            placeholder="www.example.com"
            style={{ marginBottom: 10 }}
            value={
              discoverPostInputs?.contentUrl
                ? discoverPostInputs.contentUrl
                : ''
            }
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                contentUrl: newText
              });
            }}
          />
          <Input
            label="Host"
            placeholder="Ex: John Doe"
            style={{ marginBottom: 5 }}
            value={discoverPostInputs?.host}
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                host: newText
              });
            }}
          />

          <Input
            label="Description"
            multiline
            placeholder="Describe the event..."
            style={{ marginVertical: 10 }}
            value={discoverPostInputs?.description}
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                description: newText
              });
            }}
          />

          <CreateImage
            showTitle={false}
            {...{ discoverPostInputs, setDiscoverPostInputs, TagInput }}
          />
        </Layout>
      </WrapItems>
    </Layout>
  );
}
