import { createEntityAdapter } from '@reduxjs/toolkit';

import { apiSlice } from './apiSlice';
import { DiscoverPost } from '../../app/types';
import { DISCOVER_LIST_PAGE_SIZE } from '../../constants/discover';

type getPostsArgs = {
  userKey: string;
  userName: string;
  pageNumber?: number;
  tag?: string;
  searchTerm?: string;
  sponsor?: boolean;
};

const postsAdapter = createEntityAdapter<DiscoverPost>({
  selectId: (post: DiscoverPost) => post._id
});

// Helper Functions

const tranformComments = (comments, userName) => comments.map((comment) => ({
  ...comment,
  numLikes: comment.likes.length,
  hasLiked: comment.likes.includes(userName)
}));

export const discoverApiSlice = apiSlice.injectEndpoints({
  overrideExisting: true,
  endpoints: (builder) => ({
    getDiscoverPosts: builder.query<any, getPostsArgs>({
      keepUnusedDataFor: 6000,
      query: ({
        userKey, pageNumber, tag, searchTerm, sponsor = true
      }) => ({
        url: `/discover/getPosts`,
        headers: {
          userKey
        },
        params: {
          tag,
          searchTerm,
          pageNumber,
          sponsor,
          pageLength: DISCOVER_LIST_PAGE_SIZE
        }
      }),
      transformResponse: (response: DiscoverPost[], _, { userName }) => {
        // Return Post To State Using Adapters
        return postsAdapter.addMany(
          postsAdapter.getInitialState({
            hasMorePages: response.length > 0
          }),
          // TODO: Refactoring likes and comments. Should maybe be done in backend
          response.map((post) => ({
            ...post,
            numLikes: post.likes.length,
            hasLiked: post.likes.includes(userName),
            comments: tranformComments(post.comments, userName)
          }))
        );
      },
      async onQueryStarted(
        {
          userKey, pageNumber, userName, searchTerm, tag, sponsor
        },
        { queryFulfilled, dispatch }
      ) {
        if (!pageNumber) {
          return;
        }
        const { data } = await queryFulfilled;

        if (data?.ids?.length > 0) {
          // Add Posts On Current Request To Page 1
          dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                pageNumber: 1,
                userName,
                searchTerm,
                tag,
                sponsor
              },
              (draft) => {
                postsAdapter.addMany(draft, postsSelectors.selectAll(data));
              }
            )
          );

          if (pageNumber > 1) {
            // Remove Cached Data From State Since We Already Added It To Page 1
            dispatch(
              discoverApiSlice.util.updateQueryData(
                'getDiscoverPosts',
                {
                  userKey,
                  pageNumber,
                  userName,
                  searchTerm,
                  tag,
                  sponsor
                },
                (draft) => {
                  draft = postsAdapter.getInitialState();
                }
              )
            );
          }
        } else {
          // No More Pages To Load
          dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                pageNumber: 1,
                userName,
                searchTerm,
                tag,
                sponsor
              },
              (draft) => {
                draft.hasMorePages = false;
              }
            )
          );
        }
      }
    }),

    createDiscoverPost: builder.mutation({
      query: ({
        userKey,
        title,
        type,
        content,
        tags,
        status,
        emoji,
        postBody,
        pinned
      }) => {
        if (type === 'contentImage' || type === 'contentEvent') {
          return {
            url: '/discover/addPost',
            method: 'POST',
            headers: { userKey },
            body: postBody
          };
        }
        return {
          url: '/discover/addPost',
          method: 'POST',
          headers: { userKey },
          body: {
            title,
            type,
            [type]: content,
            tags,
            status,
            emoji,
            pinned
          }
        };
      },
      async onQueryStarted(
        { userKey, userName },
        { dispatch, queryFulfilled }
      ) {
        try {
          const { data: newPost } = await queryFulfilled;

          // Add New Post To State
          dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              { userKey, userName, pageNumber: 1 },
              (draft) => {
                postsAdapter.addOne(draft, {
                  ...newPost,
                  numLikes: 0,
                  hasLiked: false
                });
              }
            )
          );
        } catch (e) {
          console.warn('Error Adding Post To State', e);
        }
      }
    }),
    deleteDiscoverPost: builder.mutation({
      query: ({
        userKey, _id, userName, title
      }) => ({
        url: '/discover/deletePost',
        method: 'DELETE',
        headers: { userKey },
        body: {
          _id,
          userName,
          title
        },
        responseHandler: (response) => response.text()
      }),
      async onQueryStarted(
        {
          userKey, _id, userName, searchTerm, tag
        },
        { dispatch, queryFulfilled }
      ) {
        if (searchTerm || tag) {
          var searchPatchResult = dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                userName,
                pageNumber: 1,
                searchTerm,
                tag
              },
              (draft) => {
                postsAdapter.removeOne(draft, _id);
              }
            )
          );
        }

        const patchResult = dispatch(
          discoverApiSlice.util.updateQueryData(
            'getDiscoverPosts',
            { userKey, userName, pageNumber: 1 },
            (draft) => {
              postsAdapter.removeOne(draft, _id);
            }
          )
        );

        try {
          await queryFulfilled;
        } catch {
          patchResult.undo();
          if (searchPatchResult) {
            searchPatchResult.undo();
          }
        }
      }
    }),

    getPostById: builder.query({
      query: ({ userKey, postId }) => ({
        url: `/discover/getPostById`,
        headers: {
          userKey
        },
        params: {
          id: postId
        }
      }),
      async onQueryStarted(
        {
          userKey, postId, userName, searchTerm, tag
        },
        { dispatch, queryFulfilled }
      ) {
        // Replace Post In State With Updated Post

        try {
          const { data: updatedPost } = await queryFulfilled;
          const updateFunction = (draft) => {
            postsAdapter.updateOne(draft, {
              id: postId,
              changes: {
                ...updatedPost,
                numLikes: updatedPost.likes.length,
                hasLiked: updatedPost.likes.includes(userName),
                comments: tranformComments(updatedPost.comments, userName)
              }
            });
          };
          dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                userName,
                pageNumber: 1
              },
              updateFunction
            )
          );
          if (searchTerm || tag) {
            dispatch(
              discoverApiSlice.util.updateQueryData(
                'getDiscoverPosts',
                {
                  userKey,
                  userName,
                  pageNumber: 1,
                  searchTerm,
                  tag
                },
                updateFunction
              )
            );
          }
        } catch (e) {
          console.warn('Error Updating Post In State', e);
        }
      }
    }),

    likePost: builder.mutation({
      query: ({
        userKey, _id, title, postAuthor
      }) => ({
        url: '/discover/like',
        method: 'PATCH',
        headers: { userKey },
        body: {
          _id,
          title,
          userName: postAuthor
        },
        responseHandler: (response) => response.text()
      }),
      async onQueryStarted(
        {
          userKey, _id, userName, searchTerm, tag
        },
        { dispatch, queryFulfilled }
      ) {
        const updateFunction = (draft) => {
          const post = postsSelectors.selectById(draft, _id);
          // Use updateOne to update the post with the given ID
          postsAdapter.updateOne(draft, {
            id: _id,
            changes: {
              numLikes: post.hasLiked ? post.numLikes - 1 : post.numLikes + 1,
              hasLiked: !post.hasLiked
            }
          });
        };

        if (searchTerm || tag) {
          var searchPatchResult = dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                pageNumber: 1,
                userName,
                searchTerm,
                tag
              },
              updateFunction
            )
          );
        }
        const patchResult = dispatch(
          discoverApiSlice.util.updateQueryData(
            'getDiscoverPosts',
            { userKey, pageNumber: 1, userName },
            updateFunction
          )
        );

        try {
          await queryFulfilled;
        } catch {
          if (searchPatchResult) {
            searchPatchResult.undo();
          }
          patchResult.undo();
        }
      }
    }),

    addComment: builder.mutation({
      query: ({ userKey, _id, text }) => ({
        url: '/discover/addComment',
        method: 'POST',
        headers: { userKey },
        body: {
          _id,
          text
        },
        responseHandler: (response) => response.text()
      }),

      async onQueryStarted(
        {
          userKey, _id, userName, searchTerm, tag
        },
        { dispatch, queryFulfilled }
      ) {
        try {
          await queryFulfilled;

          // Call getPostById to update the post in state
          dispatch(
            discoverApiSlice.endpoints.getPostById.initiate({
              userKey,
              postId: _id,
              userName,
              searchTerm,
              tag
            })
          );
        } catch (e) {
          console.warn('Error Adding Comment To State', e);
        }
      }
    }),

    likeComment: builder.mutation({
      query: ({ userKey, postId, commentId }) => ({
        url: '/discover/likeComment',
        method: 'PATCH',
        headers: { userKey },
        body: {
          postId,
          commentId
        },
        responseHandler: (response) => response.text()
      }),
      async onQueryStarted(
        {
          userKey, postId, commentId, userName, searchTerm, tag
        },
        { dispatch, queryFulfilled }
      ) {
        const updateFunction = (draft) => {
          const post = postsSelectors.selectById(draft, postId);
          // Use updateOne to update the post with the given ID
          postsAdapter.updateOne(draft, {
            id: postId,
            changes: {
              comments: post.comments.map((comment) => {
                if (comment._id === commentId) {
                  return {
                    ...comment,
                    numLikes: comment.hasLiked
                      ? comment.numLikes - 1
                      : comment.numLikes + 1,
                    hasLiked: !comment.hasLiked
                  };
                }
                return comment;
              })
            }
          });
        };
        if (searchTerm || tag) {
          var searchPatchResult = dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                pageNumber: 1,
                userName,
                searchTerm,
                tag
              },
              updateFunction
            )
          );
        }

        const patchResult = dispatch(
          discoverApiSlice.util.updateQueryData(
            'getDiscoverPosts',
            { userKey, userName, pageNumber: 1 },
            updateFunction
          )
        );

        try {
          await queryFulfilled;
        } catch {
          patchResult.undo();
          if (searchPatchResult) {
            searchPatchResult.undo();
          }
        }
      }
    }),

    deleteComment: builder.mutation({
      query: ({ userKey, postId, commentId }) => ({
        url: '/discover/deleteComment',
        method: 'DELETE',
        headers: { userKey },
        body: {
          postId,
          commentId
        },
        responseHandler: (response) => response.text()
      }),
      async onQueryStarted(
        {
          userKey, postId, commentId, userName, searchTerm, tag
        },
        { dispatch, queryFulfilled }
      ) {
        const updateFunction = (draft) => {
          const post = postsSelectors.selectById(draft, postId);
          // Use updateOne to update the post with the given ID
          postsAdapter.updateOne(draft, {
            id: postId,
            changes: {
              comments: post.comments.filter(
                (comment) => comment._id !== commentId
              )
            }
          });
        };
        const patchResult = dispatch(
          discoverApiSlice.util.updateQueryData(
            'getDiscoverPosts',
            {
              userKey,
              userName,
              pageNumber: 1
            },
            updateFunction
          )
        );

        if (searchTerm || tag) {
          var searchPatchResult = dispatch(
            discoverApiSlice.util.updateQueryData(
              'getDiscoverPosts',
              {
                userKey,
                userName,
                pageNumber: 1
              },
              updateFunction
            )
          );
        }

        try {
          await queryFulfilled;
        } catch {
          patchResult.undo();
          if (searchPatchResult) {
            searchPatchResult.undo();
          }
        }
      }
    })
  })
});

const postsSelectors = postsAdapter.getSelectors();

export { postsAdapter, postsSelectors };

export const {
  useLazyGetDiscoverPostsQuery,
  useCreateDiscoverPostMutation,
  useDeleteDiscoverPostMutation,
  useLikePostMutation,
  useAddCommentMutation,
  useLazyGetPostByIdQuery,
  useLikeCommentMutation,
  useDeleteCommentMutation
} = discoverApiSlice;
