import { FontAwesome5 } from '@expo/vector-icons';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { Layout, Text, useTheme } from '@ui-kitten/components';
import React, { useState } from 'react';
import {
  Image,
  Platform,
  TouchableOpacity,
  TouchableWithoutFeedback
} from 'react-native';

import { selectDarkMode } from '../../app/appDataSlice';
import { useAppSelector } from '../../app/hooks';
import Header from '../../components/Header';
import SearchBar from '../../components/SearchBar';
import { SponsorPage } from '../sponsors/SponsorsList';
import CreatePost from './CreatePost/CreatePost';
import PostsList from './PostsList';
import SearchPage from './SearchPage';
import SinglePost from './SinglePost/SinglePostPage';

const Stack = createNativeStackNavigator();

export default function Discover({ navigation }) {
  const theme = useTheme();
  const darkMode = useAppSelector(selectDarkMode);
  const [searchTerm, setSearchTerm] = useState('');

  const accessoryRight = (
    <TouchableOpacity onPress={() => navigation.navigate('CreatePost')}>
      <FontAwesome5 name="plus" size={23} color={theme['color-primary-500']} />
    </TouchableOpacity>
  );

  const accessoryLeft = (
    <Image
      style={{
        height: 28,
        width: 28
      }}
      source={require('../../../assets/logos/app-logo-transparent.png')}
    />
  );

  let searchBarBackgroundColor = '';
  if (darkMode) {
    searchBarBackgroundColor = theme['color-basic-800'];
  } else {
    searchBarBackgroundColor = theme['color-basic-100'];
  }

  return (
    <Stack.Navigator>
      <Stack.Screen
        name="PostsList"
        initialParams={{ sponsor: true }}
        component={PostsList}
        options={{
          title: 'Discover',
          header: () => (
            <Layout level="2" style={{ paddingBottom: 15 }}>
              <Header
                level="2"
                title="Discover"
                accessoryLeft={accessoryLeft}
                accessoryRight={accessoryRight}
                center={
                  <Layout level="2" style={{ flex: 1, marginHorizontal: 10 }}>
                    {Platform.OS === 'ios' ? (
                      <SearchBar
                        inputStyle={{
                          opacity: 0.8,
                          backgroundColor: searchBarBackgroundColor
                        }}
                        searchTerm={searchTerm}
                        onChangeText={setSearchTerm}
                        placeholder="Search Posts..."
                        inputProps={{
                          disabled: true,
                          onPressIn: () => {
                            navigation.navigate('SearchPage');
                          },

                          onBlur: () => {
                            navigation.goBack();
                          }
                        }}
                      />
                    ) : (
                      <Layout
                        style={{
                          minHeight: 35,
                          paddingLeft: 15,
                          borderRadius: 20,
                          flex: 1,
                          borderWidth: 1,
                          borderColor: darkMode
                            ? theme['color-basic-500']
                            : theme['color-basic-100'],
                          justifyContent: 'center'
                        }}
                      >
                        <TouchableWithoutFeedback
                          onPress={() => {
                            navigation.navigate('SearchPage');
                          }}
                        >
                          <Text
                            style={{
                              color: theme['color-basic-400'],
                              fontSize: 14
                            }}
                          >
                            Search Posts...
                          </Text>
                        </TouchableWithoutFeedback>
                      </Layout>
                    )}
                  </Layout>
                }
              />
            </Layout>
          )
        }}
      />
      <Stack.Screen
        name="CreatePost"
        component={CreatePost}
        options={{ headerTitle: 'Create a Post' }}
      />
      <Stack.Screen
        name="SinglePost"
        component={SinglePost}
        options={{
          headerTitle: '',
          title: 'Discover'
        }}
      />
      <Stack.Screen
        name="SearchPage"
        component={SearchPage}
        options={{ headerShown: false, animation: 'fade', title: 'Discover' }}
      />
      <Stack.Screen
        name="SponsorPageDiscover"
        component={SponsorPage}
        options={{ headerTitle: '', title: 'Discover' }}
      />
    </Stack.Navigator>
  );
}
