import { FontAwesome } from '@expo/vector-icons';
import { useTheme } from '@ui-kitten/components';
import { forwardRef, useImperativeHandle, useRef, useState } from 'react';
import { Animated, Easing } from 'react-native';

export default forwardRef(
  ({ style, iconStyle }: { style: object; iconStyle?: object }, ref) => {
    useImperativeHandle(ref, () => ({
      animate
    }));

    const opacityAnim = useRef(new Animated.Value(0)).current;
    const scaleAnim = useRef(new Animated.Value(1)).current;
    const [heartRotation, setHeartRotation] = useState('0deg');

    const pickRandomDegree = () => {
      const options = ['0deg', '15deg', '-15deg'];
      return options[Math.floor(Math.random() * options.length)];
    };

    const animate = () => {
      // animation: shrink quickly with bounce, opacity 0 - 1. Stop for a moment. Increase in size. Opacity 1-0
      setHeartRotation(pickRandomDegree());

      scaleAnim.setValue(1);
      opacityAnim.setValue(0);

      Animated.parallel([
        Animated.sequence([
          Animated.timing(opacityAnim, {
            toValue: 1,
            duration: 200,
            useNativeDriver: true
          }),
          Animated.timing(opacityAnim, {
            toValue: 1,
            duration: 300,
            useNativeDriver: true
          }),
          Animated.timing(opacityAnim, {
            toValue: 0,
            duration: 100,
            useNativeDriver: true
          })
        ]),
        ,
        Animated.sequence([
          Animated.timing(scaleAnim, {
            toValue: 0.8,
            easing: Easing.bounce,
            duration: 200,
            useNativeDriver: true
          }),
          Animated.timing(scaleAnim, {
            toValue: 0.8,
            duration: 300,
            useNativeDriver: true
          }),
          Animated.timing(scaleAnim, {
            toValue: 1.2,
            duration: 100,
            useNativeDriver: true
          })
        ])
      ]).start();
    };

    return (
      <Animated.View
        style={{
          ...style,
          opacity: opacityAnim,
          transform: [{ scale: scaleAnim }]
        }}
      >
        <FontAwesome
          name="heart"
          size={110}
          style={{ transform: [{ rotate: heartRotation }], ...iconStyle }}
        />
      </Animated.View>
    );
  }
);
