import { Feather, FontAwesome } from '@expo/vector-icons';
import { Input, Layout, Text } from '@ui-kitten/components';
import * as ImagePicker from 'expo-image-picker';
import React from 'react';
import {
  Alert,
  Dimensions,
  Image,
  Platform,
  TouchableOpacity
} from 'react-native';
import Toast from 'react-native-root-toast';

const windowWidth = Dimensions.get('window').width;

export default function CreateImage({
  discoverPostInputs,
  setDiscoverPostInputs,
  TagInput,
  showTitle = true
}) {
  const [imagePickerStatus, requestImagePickerPermission] =
    ImagePicker.useMediaLibraryPermissions();
  const [cameraStatus, requestCameraPermission] =
    ImagePicker.useCameraPermissions();

  const onPressCamera = async () => {
    const response = await requestCameraPermission();
    if (response.granted || cameraStatus.granted) {
      const result = await ImagePicker.launchCameraAsync({
        mediaTypes: ImagePicker.MediaTypeOptions.Images
      });
      if (!result.canceled) {
        setDiscoverPostInputs({
          ...discoverPostInputs,
          contentImage: result.assets[0]
        });
      }
    } else {
      Alert.alert(
        "Permission to access camera is required. Please enable it in your phone's settings."
      );
    }
  };

  const pickImage = async () => {
    const response = await requestImagePickerPermission();
    if (response.granted || imagePickerStatus.granted) {
      const result = await ImagePicker.launchImageLibraryAsync({
        allowsEditing: false
      });
      if (!result.canceled) {
        setDiscoverPostInputs({
          ...discoverPostInputs,
          contentImage: result.assets[0]
        });

        if (Platform.OS === 'android') {
          Toast.show("Don't worry if the image seems blurry on the sides.", {
            duration: Toast.durations.LONG,
            opacity: 1,
            position: -100,
            backgroundColor: 'grey',
            shadow: false
          });
        }
      }
    } else {
      Alert.alert(
        "Permission to access camera roll is required. Please enable it in your phone's settings."
      );
    }
  };

  const Title = (
    <Layout
      style={{
        flexDirection: 'row',
        justifyContent: 'space-evenly',
        alignItems: 'center',
        marginTop: 15
      }}
    >
      <Input
        label="Title"
        placeholder="Post Title"
        value={discoverPostInputs?.title ? discoverPostInputs.title : ''}
        style={{ flex: 5 }}
        onChangeText={(newText) => {
          setDiscoverPostInputs({
            ...discoverPostInputs,
            title: newText
          });
        }}
      />
    </Layout>
  );

  let imagePicker = <></>;

  if (discoverPostInputs?.contentImage) {
    // Image Preview
    imagePicker = (
      <Layout
        level="2"
        style={{
          height: 300,
          marginVertical: 10,
          borderRadius: 10,
          shadowColor: '#000',
          shadowOffset: {
            width: 0,
            height: 5
          },
          shadowOpacity: 0.34,
          shadowRadius: 6.27,

          elevation: 10
        }}
      >
        <TouchableOpacity
          onPress={() => {
            setDiscoverPostInputs(null);
          }}
          style={{
            position: 'absolute',
            right: 10,
            top: 5,
            zIndex: 1
          }}
        >
          <Feather name="x-circle" size={30} color="grey" />
        </TouchableOpacity>

        <Image
          source={{
            uri: discoverPostInputs.contentImage.uri
          }}
          style={{
            borderRadius: 5,
            width: '100%',
            height: '100%',
            resizeMode: 'contain'
          }}
        />
      </Layout>
    );
  } else {
    // Image Picker
    imagePicker = (
      <Layout style={{ flexDirection: 'row' }}>
        <TouchableOpacity
          onPress={() => {
            pickImage();
          }}
          style={{
            height: 80,
            justifyContent: 'center',
            alignItems: 'center',
            flex: 1,
            paddingVertical: 8,
            flexDirection: 'row'
          }}
        >
          <Layout
            level="2"
            style={{
              flex: 1,
              flexDirection: 'row',
              height: '100%',
              marginHorizontal: 10,
              paddingVertical: 15,
              paddingHorizontal: 5,
              borderRadius: 8,
              alignItems: 'center',
              justifyContent: 'space-around'
            }}
          >
            <FontAwesome name="image" size={25} color="grey" />
            <Text>Select Image</Text>
          </Layout>
        </TouchableOpacity>
        <TouchableOpacity
          onPress={onPressCamera}
          style={{
            height: 80,
            justifyContent: 'center',
            alignItems: 'center',
            flex: 1,
            paddingVertical: 8,
            flexDirection: 'row'
          }}
        >
          <Layout
            level="2"
            style={{
              flex: 1,
              flexDirection: 'row',
              height: '100%',
              marginHorizontal: 10,
              paddingVertical: 15,
              paddingHorizontal: 5,
              borderRadius: 8,
              alignItems: 'center',
              justifyContent: 'space-around'
            }}
          >
            <FontAwesome name="camera" size={25} color="grey" />
            <Text>Use Camera</Text>
          </Layout>
        </TouchableOpacity>
      </Layout>
    );
  }

  return (
    <>
      {showTitle && Title}
      {TagInput()}
      {imagePicker}
    </>
  );
}
