import { Card, Modal, Text, Button } from '@ui-kitten/components';
import { StyleSheet } from 'react-native';

export default function DisabledFieldModal({ setShowModal }) {
  return (
    <Modal
      visible
      backdropStyle={myStyles.backdrop}
      style={{ width: '75%' }}
      onBackdropPress={() => setShowModal(false)}
    >
      <Card disabled>
        <Text style={{ fontSize: 15, paddingTop: 5, paddingBottom: 10 }}>
          Sorry, we are experiencing technical difficulties and this
          functionality is currently undergoing maintenance. Please try again
          later.
        </Text>
        <Button
          onPress={() => {
            setShowModal(false);
          }}
        >
          Dismiss
        </Button>
      </Card>
    </Modal>
  );
}

const myStyles = StyleSheet.create({
  backdrop: {
    backgroundColor: 'rgba(0, 0, 0, 0.65)'
  }
});
