import { FontAwesome5 } from '@expo/vector-icons';
import {
  Text,
  Input,
  Select,
  SelectItem,
  IndexPath,
  Layout,
  Button,
  Spinner,
  useTheme,
  CheckBox,
  Divider
} from '@ui-kitten/components';
import React, { useState } from 'react';
import {
  StyleSheet,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Keyboard,
  ScrollView,
  KeyboardAvoidingView,
  Platform
} from 'react-native';
import Toast from 'react-native-root-toast';

import CreateEventPost from './CreateEventPost';
import CreateImage from './CreateImagePost';
import { selectDarkMode } from '../../../app/appDataSlice';
import { useAppSelector } from '../../../app/hooks';
import {
  tagIndices,
  adminTagIndices,
  postOptions
} from '../../../constants/discover';
import { showToast } from '../../../functions';
import { useCreateDiscoverPostMutation } from '../../api/discoverApi';
import { usersApiSlice } from '../../api/usersApi';

export function OptionalText() {
  return (
    <Text
      style={{
        alignSelf: 'flex-end',
        marginRight: 5,
        color: 'grey',
        fontSize: 11
      }}
    >
      Optional
    </Text>
  );
}

export default function CreatePost() {
  const theme = useTheme();
  const darkMode = useAppSelector(selectDarkMode);
  const userKey = useAppSelector((state) => state.appData.secure.userKey);
  const {
    data: { userName, permissionLevel }
  } = usersApiSlice.endpoints.getUserInfo.useQueryState(userKey);
  const [discoverPostInputs, setDiscoverPostInputs] = useState(null);
  const [startTime, setStartTime] = useState(new Date());
  const [endTime, setEndTime] = useState(new Date());
  const [date, setDate] = useState(new Date());
  const [selectedTagIndex, setSelectedTagIndex] = useState<IndexPath[]>([]);
  const [selectedPostTypeIndex, setSelectedPostTypeIndex] = useState(
    new IndexPath(0)
  );
  const [error, setError] = useState(null);
  const [isEmojiShown, setIsEmojiShown] = useState(false);

  const [createDiscoverPost, { isLoading }] = useCreateDiscoverPostMutation();

  const tagArray = permissionLevel === 'admin' ? adminTagIndices : tagIndices;

  const submitEvent = async (tags: string[]) => {
    // Validate date and time
    if (date < new Date()) {
      setError('Date must be in the future');
      return;
    }

    if (startTime > endTime) {
      setError('Start time must be before end time');
      return;
    }

    const formdata = new FormData();
    formdata.append('type', 'contentEvent');
    formdata.append('title', discoverPostInputs.title);
    formdata.append('location', discoverPostInputs.location);
    formdata.append('date', date.toISOString());
    formdata.append('startTime', startTime.toISOString());
    formdata.append('endTime', endTime.toISOString());

    // Optional fields
    if (discoverPostInputs.contentUrl)
      formdata.append('contentUrl', discoverPostInputs.contentUrl);
    if (discoverPostInputs.host)
      formdata.append('host', discoverPostInputs.host);
    if (discoverPostInputs.description)
      formdata.append('contentText', discoverPostInputs.description);
    if (discoverPostInputs.contentImage) {
      formdata.append('picture', {
        uri: discoverPostInputs.contentImage.uri,
        name: 'discoverImage',
        type: 'image/jpg'
      } as any);
    }

    tags.forEach((tag) => {
      formdata.append('tags', tag);
    });

    try {
      await createDiscoverPost({
        type: 'contentEvent',
        userKey,
        userName,
        postBody: formdata
      }).unwrap();

      showToast('Posted Successfully!', theme['color-success-500']);

      setDiscoverPostInputs(null);
      setSelectedTagIndex([]);
      setError(null);
    } catch (e) {
      console.warn("Couldn't post image", e);
      showToast('Post failed to send.', theme['color-warning-500']);
    }
  };

  const submitImage = async (tags: string[]) => {
    const formdata = new FormData();
    formdata.append('title', discoverPostInputs.title);
    formdata.append('type', 'contentImage');
    formdata.append('picture', {
      uri: discoverPostInputs.contentImage.uri,
      name: 'discoverImage',
      type: 'image/jpg'
    } as any);
    tags.forEach((tag) => {
      formdata.append('tags', tag);
    });

    try {
      await createDiscoverPost({
        type: 'contentImage',
        userKey,
        userName,
        postBody: formdata
      }).unwrap();

      showToast('Posted Successfully!', theme['color-success-500']);

      setDiscoverPostInputs(null);
      setSelectedTagIndex([]);
      setError(null);
    } catch (e) {
      console.warn("Couldn't post image", e);
      showToast('Post failed to send.', theme['color-warning-500']);
    }
  };

  const onSubmit = async () => {
    //
    // Validate Input
    //
    if (
      (selectedPostTypeIndex.row === 1 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.contentText)) ||
      (selectedPostTypeIndex.row === 2 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.contentUrl)) ||
      (selectedPostTypeIndex.row === 0 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.contentImage)) ||
      (selectedPostTypeIndex.row === 3 &&
        (!discoverPostInputs ||
          !discoverPostInputs.title ||
          !discoverPostInputs.location ||
          !date ||
          !startTime ||
          !endTime))
    ) {
      console.warn('onsubmit error', discoverPostInputs);
      setError('Please Fill in All Fields');
      return;
    }

    //
    // Submit Post
    //

    const tags = selectedTagIndex.map((index) => tagArray[index.row]);

    // Special Case for Image Post
    if (selectedPostTypeIndex.row === 0) {
      submitImage(tags);
      return;
    }

    // Special Case for Event Post
    if (selectedPostTypeIndex.row === 3) {
      submitEvent(tags);
      return;
    }

    let type = '';
    if (selectedPostTypeIndex.row === 1) {
      type = 'contentText';
    } else if (selectedPostTypeIndex.row === 2) {
      type = 'contentUrl';
    }
    try {
      await createDiscoverPost({
        type,
        userKey,
        userName,
        tags,
        content: discoverPostInputs[type],
        title: discoverPostInputs.title,
        emoji: discoverPostInputs.emoji
      }).unwrap();

      const toast = Toast.show('Posted Successfully!', {
        duration: Toast.durations.LONG,
        opacity: 1,
        position: -100,
        backgroundColor: theme['color-success-500'],
        shadow: false
      });

      setTimeout(() => {
        Toast.hide(toast);
      }, 7000);

      setDiscoverPostInputs(null);
      setSelectedTagIndex([]);
      setError(null);
    } catch (e) {
      const toast = Toast.show('Post failed to send.', {
        duration: Toast.durations.LONG,
        opacity: 1,
        position: -100,
        backgroundColor: theme['color-warning-500'],
        shadow: false
      });

      setTimeout(() => {
        Toast.hide(toast);
      }, 4000);
    }
  };

  function TagInput() {
    const groupedData = selectedTagIndex.map((index) => {
      return tagArray[index.row];
    });
    return (
      <Layout>
        <Select
          label="Tags"
          multiSelect
          value={groupedData.join(', ')}
          placeholder="Select Tags"
          selectedIndex={selectedTagIndex}
          onSelect={(index: IndexPath[]) => {
            setSelectedTagIndex(index);
          }}
          style={styles.input}
        >
          {tagArray.map((title, i) => (
            <SelectItem title={title} key={i} />
          ))}
        </Select>
        <OptionalText />
      </Layout>
    );
  }

  const renderPostOptions = () => {
    // Text
    if (selectedPostTypeIndex.row === 1) {
      return (
        <>
          <Layout
            style={{
              flexDirection: 'row',
              justifyContent: 'space-evenly',
              alignItems: 'center',
              marginTop: 15
            }}
          >
            {/* <EmojiToggle /> */}

            <Input
              placeholder="Post Title"
              onFocus={() => {
                setIsEmojiShown(false);
              }}
              value={discoverPostInputs?.title ? discoverPostInputs.title : ''}
              style={{ ...styles.input, flex: 5 }}
              onChangeText={(newText) => {
                setDiscoverPostInputs({
                  ...discoverPostInputs,
                  title: newText
                });
              }}
            />
          </Layout>
          {TagInput()}
          <Input
            multiline
            placeholder="Post Body"
            onFocus={() => {
              setIsEmojiShown(false);
            }}
            textStyle={{ minHeight: 100 }}
            style={{ ...styles.input, alignSelf: 'center', width: '100%' }}
            value={
              discoverPostInputs?.contentText
                ? discoverPostInputs.contentText
                : ''
            }
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                contentText: newText
              });
            }}
          />
        </>
      );
    }
    if (selectedPostTypeIndex.row === 2) {
      // LINK
      return (
        <>
          <Layout
            style={{
              flexDirection: 'row',
              justifyContent: 'space-evenly',
              alignItems: 'center',
              marginTop: 15
            }}
          >
            {/* <EmojiToggle /> */}
            <Input
              placeholder="Post Title"
              onFocus={() => {
                setIsEmojiShown(false);
              }}
              value={discoverPostInputs?.title ? discoverPostInputs.title : ''}
              style={{ ...styles.input, flex: 5 }}
              onChangeText={(newText) => {
                setDiscoverPostInputs({
                  ...discoverPostInputs,
                  title: newText
                });
              }}
            />
          </Layout>

          <Input
            placeholder="Post Link"
            style={styles.input}
            onFocus={() => {
              setIsEmojiShown(false);
            }}
            value={
              discoverPostInputs?.contentUrl
                ? discoverPostInputs.contentUrl
                : ''
            }
            onChangeText={(newText) => {
              setDiscoverPostInputs({
                ...discoverPostInputs,
                contentUrl: newText
              });
            }}
          />
          {/* {PostTypeInput} */}
          {TagInput()}
        </>
      );
    }
    if (selectedPostTypeIndex.row === 0) {
      // IMAGE
      return (
        <CreateImage
          discoverPostInputs={discoverPostInputs}
          setDiscoverPostInputs={setDiscoverPostInputs}
          TagInput={TagInput}
        />
      );
    }
    if (selectedPostTypeIndex.row === 3) {
      // EVENT
      return (
        <CreateEventPost
          discoverPostInputs={discoverPostInputs}
          setDiscoverPostInputs={setDiscoverPostInputs}
          TagInput={TagInput}
          startTime={startTime}
          setStartTime={setStartTime}
          endTime={endTime}
          setEndTime={setEndTime}
          date={date}
          setDate={setDate}
        />
      );
    }
  };

  return (
    <Layout style={{ flex: 1 }}>
      <KeyboardAvoidingView
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
        style={{ flex: 1 }}
      >
        <ScrollView style={{ flex: 1 }}>
          <TouchableWithoutFeedback
            style={{ flex: 1 }}
            onPress={() => {
              Keyboard.dismiss();
            }}
          >
            <Layout
              style={{ flex: 1, marginBottom: 100, marginHorizontal: 16 }}
            >
              <Select
                status="success"
                label="Select Post Type"
                value={postOptions[selectedPostTypeIndex.row]}
                selectedIndex={selectedPostTypeIndex}
                onSelect={(index: IndexPath) => {
                  setSelectedPostTypeIndex(index);
                  setSelectedTagIndex([]);
                  setDiscoverPostInputs(null);
                  setError(null);
                  setIsEmojiShown(false);
                }}
                style={styles.input}
              >
                <SelectItem
                  title="Image Post"
                  accessoryLeft={
                    <FontAwesome5
                      name="image"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
                <SelectItem
                  title="Text Post"
                  accessoryLeft={
                    <FontAwesome5
                      name="file-alt"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
                <SelectItem
                  title="Link"
                  accessoryLeft={
                    <FontAwesome5
                      name="link"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
                <SelectItem
                  title="Event"
                  accessoryLeft={
                    <FontAwesome5
                      name="calendar-alt"
                      size={17}
                      color={darkMode ? '#fff' : '#000'}
                    />
                  }
                />
              </Select>

              <Divider />
              {renderPostOptions()}
              {error && (
                <Layout>
                  <Text
                    style={{
                      textAlign: 'center',
                      color: 'red',
                      fontStyle: 'italic'
                    }}
                  >
                    {error}
                  </Text>
                </Layout>
              )}
              {!isEmojiShown && (
                <Button
                  style={styles.input}
                  onPress={onSubmit}
                  disabled={isLoading}
                >
                  Submit
                </Button>
              )}
            </Layout>
          </TouchableWithoutFeedback>
        </ScrollView>
      </KeyboardAvoidingView>
      {isLoading && (
        <Layout
          style={{
            justifyContent: 'center',
            alignItems: 'center',
            width: '100%',
            height: '100%',
            position: 'absolute',
            backgroundColor: 'black',
            flex: 1,
            opacity: 0.3
          }}
        >
          <Spinner size="medium" />
        </Layout>
      )}
    </Layout>
  );
}

const styles = StyleSheet.create({
  input: {
    marginVertical: 5
  }
});
