import { Layout, Text } from '@ui-kitten/components';
import { Dimensions, TouchableOpacity } from 'react-native';

import { MAX_WIDTH_WEB } from '../../../constants/discover';
import BottomBar from '../BottomBar';
import ImageComponent from '../ImageComponent';

const windowWidth = Dimensions.get('window').width;

export default function ImageCard({
  title,
  _id,
  navigation,
  imageId,
  tags,
  postAuthor,
  userName,
  searchTerm,
  searchedTag,
  pinnedIcon,
  hasLiked
}) {
  return (
    <Layout>
      {pinnedIcon}
      <Layout style={{ flex: 1, alignItems: 'center' }}>
        <TouchableOpacity
          onPress={() => {
            navigation.navigate('SinglePost', { _id, searchTerm, searchedTag });
          }}
          activeOpacity={0.9}
          style={{
            flex: 1,
            alignItems: 'center',
            paddingVertical: 10
          }}
        >
          <Layout
            style={{
              width: windowWidth * 0.9,
              maxWidth: MAX_WIDTH_WEB,
              paddingVertical: 10
            }}
          >
            <Text style={{ fontSize: 20, marginLeft: 15 }}>{title}</Text>
          </Layout>
          <ImageComponent
            apiInfo={{
              _id,
              title,
              postAuthor,
              userName,
              searchTerm,
              searchedTag
            }}
            imageId={imageId}
            hasLiked={hasLiked}
          />

          <BottomBar
            tags={tags}
            _id={_id}
            searchTerm={searchTerm}
            searchedTag={searchedTag}
          />
        </TouchableOpacity>
      </Layout>
    </Layout>
  );
}
